<template>
<el-card class="card_back">
    <template #header>
        <el-button size="mini" @click="addinit" type="primary" v-if="utils.hasLoginComponents('sys:app:add')" icon="el-icon-circle-plus-outline">新增</el-button>
    </template>
    <table-page :url="tableinit.url" :datatitle="tableinit.datatitle" :parems="tableinit.parems" :opreate="tableinit.opreate"></table-page>
    <el-dialog v-if="formshow" :visible.sync="formshow" width="700px" @close="utils.formreset($refs['appForm'])">
        <el-form ref="appForm" :model="appInfor" label-width="120px" label-position="right" :rules="rules">
            <el-form-item label="应用系统名称" prop="system_name">
                <el-input v-model="appInfor.system_name" placeholder="请输入应用系统名称"></el-input>
            </el-form-item>
            <el-form-item label="应用系统备注" prop="remarks">
                <el-input v-model="appInfor.remarks" placeholder="请输入需要应用系统备注信息"></el-input>
            </el-form-item>
            <el-form-item label="描述" prop="detail">
                <el-input type="textarea" rows="5" v-model="appInfor.detail" placeholder="请输入需要应用系统描述信息"></el-input>
            </el-form-item>
        </el-form>
        <template #footer>
            <el-button type="primary" @click.native="add('appForm')">提交</el-button>
            <el-button type="warning" @click.native="utils.formreset($refs['appForm'])">重置</el-button>
        </template>
    </el-dialog>
    <multiple-selection-form :visible.sync="funassignshow" :parems="multipleinit.parems" :title="multipleinit.title" :url="multipleinit.url" :width="multipleinit.width" :datatitle="multipleinit.datatitle"></multiple-selection-form>
</el-card>
</template>

<script>
export default {
    components: {
        tablePage: () => import('@/components/tablePage.vue'),
        multipleSelectionForm: () => import('@/components/multipleSelectionForm.vue')
    },
    data() {
        let vm = this;
        const has = vm.utils.hasLoginComponents;
        return {
            formshow: false,
            funassignshow: false, //功能分配弹窗
            addorup: 1, //1表示新增，0表示修改 必需属性
            appInfor: {
                system_name: "",
                reamrks: "",
                detail: ""
            },
            tableinit: {
                url: "/selectManageSystemPaging.action",
                parems: {},
                datatitle: [{
                    prop: 'application_system_id',
                    label: '应用系统编号',
                    width: '200'
                }, {
                    prop: 'system_name',
                    label: '应用系统名称',
                    width: '200'
                }, {
                    prop: 'remarks',
                    label: '备注',
                    width: '300'
                }, {
                    prop: 'detail',
                    label: '描述',
                    width: '400'
                }],
                opreate: {
                    label: "操作",
                    minwidth: "300",
                    btn: [{
                        type: "info",
                        size: 'mini',
                        text: "功能分配",
                        icon: "el-icon-tickets",
                        btnfun: vm.funAssignment,
                        show: has('sys:app:distribute')
                    }, {
                        type: "warning",
                        size: 'mini',
                        text: "修改",
                        icon: "el-icon-edit",
                        btnfun: vm.update,
                        show: has('sys:app:modify')
                    }, {
                        type: "danger",
                        size: 'mini',
                        icon: "el-icon-delete",
                        text: "删除",
                        btnfun: vm.delete,
                        show: has('sys:app:delete')
                    }]
                }
            },
            rules: {
                system_name: [{
                    trigger: 'blur',
                    required: true,
                    message: '应用系统名称不能为空',
                }, {
                    trigger: 'blur',
                    validator: vm.$validateApi.cnLength,
                    len: 20,
                    name: '应用系统名称'
                }, {
                    trigger: 'blur',
                    validator: vm.$validateApi.findDuplicateData,
                    name: '应用系统名称',
                    findName: 'app_sys_name'
                }],
                remarks: [{
                    trigger: 'blur',
                    required: true,
                    message: '备注不能为空',
                }],
                detail: [{
                    trigger: 'blur',
                    required: true,
                    message: '应用系统详情描述不能为空',
                }]
            },
            multipleinit: {
                parems: {},
                title: "应用系统功能分配",
                url: "",
                updateurl: "",
                datatitle: [{
                    prop: "id",
                    minwidth: '15%',
                    label: '功能编号'
                }, {
                    prop: "name",
                    minwidth: '15%',
                    label: '功能名称'
                }, {
                    prop: "type",
                    minwidth: '30%',
                    label: '功能类型'
                }, {
                    prop: "detail",
                    minwidth: '30%',
                    label: '功能描述'
                }],
            }
        }
    },
    methods: {
        funAssignment(row) {
            this.funassignshow = true;
            this.multipleinit.parems = {
                id: row.application_system_id
            };
        },
        addinit() {
            let vm = this;
            vm.addorup = 1;
            vm.appInfor = {};
            vm.formshow = true;
        },
        add(ref) {
            let vm = this;
            let addorup = vm.addorup;
            vm.$refs[ref].validate((valreq) => {
                if (valreq) {
                    vm.$api.opreateRequset(addorup ? '/addSystemManage.action' : '/updataSystemManage.action', vm.utils.data.formatter(vm.appInfor), addorup ? '添加' : '修改').then(success => {
                        if (success) {
                            vm.formshow = false;
                        }
                    });
                }
            })
        },
        update(row) {
            let vm = this;
            vm.addorup = 0;
            vm.appInfor = row;
            vm.formshow = true;
        },
        delete(row) {
            let vm = this;
            let datas = {
                id: row.application_system_id,
            };
            vm.$api.deleteRequset('/deleteSystemManage.action', vm.utils.data.formatter(datas));
        }
    }
}
</script>

<style>

</style>
